<template>
	<div id="videobox">
		<ul id="videnav" class="col-md-12">
			<li v-for="(item,index) in list"  >
				<div class="flo1 flo2" style="position: relative;z-index:5; margin-bottom: 8px;">
					
					<router-link :to="{path:'playvideo',query:{playurl:item.data.content.data.playUrl,imgurl:item.data.content.data.cover.feed}}">
						<img :src="item.data.content.data.cover.feed" />
						<i class="iconfont icon-bofang-copy" style="width: 30px;height: 30px;"></i>
				    </router-link>
					<!-- <video :src="item.data.content.data.playUrl"></video> -->
					
				</div>
				<div class="flo1">
				<div>{{item.data.content.data.title}}</div>
				  <div id="f1">
				  	<span>作者:{{item.data.content.data.author.name}} </span>
				  	<span>类型:{{item.data.content.data.category}}</span>
				  	
				  </div>
				    <p>
						<a>
							 <span class="iconfont icon-heart-fill" style="width: 30px;height: 30px;">{{item.data.content.data.duration}}万</span>
							
						</a>
					</p>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				list:[]
			};
		},
		created() {
			this.$http.get("https://api.apiopen.top/todayVideo").then(response => {
				console.log(response.body.result)			
				       var a=[] 
		          response.body.result.forEach(function(val,index,arr){
					console.log(val)
					if(val.data.content){
						a.push(val)
				     }else{
						 
					 }
					//
					//this.list=a
				    //console.log(a)
				  })
				  
				// get body data
				
				this.list = a 
				
		
			}, response => {
				// error callback
			});
		}
	}
</script>

<style scoped="">
	#videobox{}
.icon-bofang-copy{position: absolute;left:50%;top:50%;font-size:48px;display: none;z-index: 2;color: orange;margin-left: -29px;margin-top: -24px;}
.flo2:hover .icon-bofang-copy{
	display:block;
}
#videnav{overflow:hidden; }
#videnav li{float:left;margin: 20px  5px;   }
#videnav li div{}
#videnav li div img{width: 280px;height: 180px;}
#videnav li .flo1{float: left;}
p a{text-decoration: none;
    color: #3a3a3a;
	}
.fi{
	width: 100%;
}	
	
.fi span{
	
	font-size: 12px;
	color: #888;
	line-height: 20px;
	margin-right:20px;
	display: inline-block;
}
</style>



