<template>
  <div id="app">
		
	<el-container>
  <el-aside width="200px" style="position:fixed;"><Contain></Contain></el-aside>
  <el-container>
    <el-header ><Headbox ></Headbox></el-header>
    <el-main style="margin-left: 200px;"><div class=""><router-view> </router-view></div></el-main>
    <!-- <el-footer  style="margin-left: 200px;"></el-footer> -->
  </el-container>
</el-container>

		 
  </div>

</template>

<script>



import Headbox from '@/components/Headbox'
import Contain from "@/components/Contain"

export default {
  name: 'App',
  components: {
   Headbox,
	 Contain
	
  }
}


</script>

<style>

.el-header, .el-footer {
    background-color:#262626;
    color: #333;
    text-align: center;
    line-height: 60px;

  }
  
  .el-aside {
		padding-top:50px;
    background-color:#262626;
   
    padding-left: 40px;
	font-size: 20px;
    line-height: 100px;
	height: 100%;

  }
  
  .el-main {
    background-color:#f9f9f9;
    /* color: #333; */
    /* text-align: center; */
    /* line-height: 160px; */
		min-width: 1250px;
  }
  
  body > .el-container {
    margin-bottom: 40px;
  }
  
  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }
  
  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
</style>



