<template>
	 <div>
<aplayer autoplay  showlrc=true :music="{
  title: list.name,
  author: list.singer,
  url: list.url,
  pic: list.pic,
  lrc: list.lrc
}">


</aplayer>
<!-- <Songdan></Songdan> -->
   <div>
		<ul id="navtitle">
			<li class="songtitle">歌曲</li>
			<li>歌手</li>
			<li>时长</li>
		</ul>
		<div>
		<ul id="navxian">
<li v-for="(item,index) in songlist.slice((currentpage-1)*pagesize,pagesize*currentpage)" >
				 <div class="boxtitle"  v-on:click="changesongid(item.id)"> {{item.name}}</div>
				 <div class="boxautor"> {{item.singer}}</div>
				 <div class="boxtime"> {{item.time}}</div>
			</li>
		</ul>
		</div>
	</div>
       <div id="page">
				<el-pagination background layout="prev, pager, next" :total="200"  @size-change="handleSizeChange"
      @current-change="handleCurrentChange" >
				</el-pagination>
			</div>
      
	 </div>
</template>

<script>

import Aplayer from 'vue-aplayer'
//import  Songdan    from  "./Songdan"
	export default {
		data() {
			return {
				list:"",
				songlist:[],
				pagesize:20,
				currentpage:1,
				songid:this.$route.query.id
			};
		},
		created() {
			//const url="https://api.bzqll.com/music/netease/song?key=579621905&id="
			console.log(this.$route.query.id)
			this.$http.get("https://api.bzqll.com/music/netease/song?key=579621905&id="+this.songid).then(response => {
				console.log(response.body.data)
		        this.list=response.body.data
			}, response => {
				// error callback
			});
			
			this.$http.get("https://api.bzqll.com/music/netease/songList?key=579621905&id=3778681&limit=10&offset=0").then(response => {
					
					this.songlist=response.body.data.songs
				  console.log(response.body.data.songs)
				}, response => {
					// error callback
				});
			
			
		},
		
		components: {
           Aplayer
		  /* "Songdan":Songdan */
      	},
		methods:{
			handleSizeChange(val) {
			  //console.log(`每页  条`);
					this.pagesize=`${val}`
			}, 
			 handleCurrentChange(val) {
			  console.log(`当前页: ${val}`);
					
					this.currentpage=`${val}`
					console.log(this.currentpage);
			},
			
				changesongid:function(id){
				 this.$http.get("https://api.bzqll.com/music/netease/song?key=579621905&id="+id).then(response => {
				 	console.log(response.body.data)
				     this.list=response.body.data
				 }, response => {
				 	// error callback
				 });
			
			} 
			
		},
		computed:{
			
		}
	}
</script>

<style scoped="">
	 #navtitle {
		 height: 50px;
    line-height: 50px;
	background-color: #fbfbfd;
	padding-left: 46px;
    padding-right: 100px;
	 }
 #navtitle li{
	  float: left;
	      padding-left: 20px;
		  font-size: 14px;
		      color: #000;
			      width: 20%;
 }
  #navtitle  .songtitle{
	  width: 54%;
  }
  #navxian{
	  overflow: hidden;
  }
   #navxian li{
	   width: 1200px;
	   height:50px;
	   
    cursor: pointer;
   }
  #navxian li div{
	  float: left;
  }
#navxian li  .boxtitle{
	    width: 54%;
		line-height: 50px;
    height: 50px;
	    font-size: 14px;
		padding-left: 4%;
		    color: #000;
}
 #navxian li   .boxautor{
	 padding: 0 10px;
	 width: 245px;
	 line-height: 50px;
 }
 #navxian li   .boxtime{
	 line-height: 50px;
 }
 #page{
	 text-align: center;
 }
</style>



