// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'

import '@/assets/iconfont.css'


import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
Vue.use(ElementUI)//全局使用ElementUI



import vueResource from "vue-resource"
Vue.use(vueResource)


import VueRouter from "vue-router"
Vue.use(VueRouter)



import VideoPlayer from 'vue-video-player'
require('video.js/dist/video-js.css')
require('vue-video-player/src/custom-theme.css')
Vue.use(VideoPlayer)




import Videobox from "@/components/Videobox"
import Fiction from "@/components/Fiction"
import Jokes from "./components/Jokes"
import Musicbox from "./components/Musicbox"
//import Register  from "@/components/Register"


import Detailsbox from "@/components/fictiones/Detailsbox"
import Playvideo from "@/components/videoes/Playvideo"
import Jokeplay from "@/components/joke/Jokeplay"
import Songlist from "@/components/music/Songlist"
import Musicconent from "@/components/music/Musicconent"

import Songplay from "@/components/music/Songplay"
import Searchresult from "@/components/music/Searchresult"

import Mvplay from "@/components/music/mvlist/Mvplay"
import Mvplayingbox from "@/components/music/mvlist/Mvplayingbox"
const routes=[
	{path:"/vieo",component:Videobox},
	{path:"/Details",component:Detailsbox},
	{path:"/",component:Fiction},
	/* {path:"/register",component:Register}, */
	{path:"/jok",component:Jokes,redirect:"/jokeplay",children:[
		{path:"/jokeplay",component:Jokeplay}
	]},
	{path:"/music",component:Musicbox,redirect:"/songlist",children:[
		{path:"/songlist",component:Songlist},
		{path:"/searchresult",component:Searchresult},
		{path:"/musicconen",component:Musicconent,redirect:"/mvplay", children:[
			{path:"/mvplay",component:Mvplay},
			{path:"/mvplayingbox",component:Mvplayingbox}
		]},
		{path:"/Songplay",component:Songplay}
	]},
	{path:"/playvideo",component:Playvideo}
	
	]



const router=new  VueRouter({
	routes,
	mode:"history"
	
})




Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
	router,
  components: { App },
  template: '<App/>'
 
})



