<template>
	<div id="mvbox">
		<div id="mvleft"><router-view  ref="ref"></router-view></div>
		<div id="mvright">
			<ul>
				<li  :class="{'active':ind == index}" v-for="(item,index) in listtag"  @click="changeboxid(item.id,index)"><router-link :to="{path:'/mvplay',query:{id:item.id}}">{{item.title}}</router-link></li>
			</ul>
		</div>
	</div>
	
</template>

<script>
	export default {
		data() {
			return {
				listtag:[],
				ind:""
			};
		},
		created() {
		
		
			this.$http.get("https://api.bzqll.com/music/tencent/mvListCategory?key=579621905").then(
				response => {
		console.log(response.body.data.tag)
					this.listtag = response.body.data.tag
					
				}, response => {
					// error callback
				});
		
		
		},
		methods:{
			changeboxid(msg,index){
				console.log(index)
				this.ind = index
				 this.$refs.ref.changeajax(msg);
			}
		}
	}
</script>

<style scoped="scoped">
#mvbox{
	overflow: hidden;
}
#mvbox #mvleft{
	float: left;
	width: 90%;
}
#mvbox #mvright{
	float: right;
	width: 10%;
}

.router-link-active {
    text-decoration: none;
	color: #3A3A3A;
	
}


.active{
	
	background:orange;
	color:#fff;
	
}
.active a{
	color:#fff;
}

#mvbox #mvright ul li a{
	color: #000;
	text-decoration: none;
	font-size: 19px;
	line-height: 26px;
}
#mvbox #mvright ul li{
	text-align: center;
	margin-bottom: 4px;
	
	
}
</style>



