<template>
	<div class="row">
		<div class="col-md-6"><router-view ref="ref"></router-view></div>
		<div class="col-md-6">
			<ul id="joknav">
				<li v-for="(item,index) in list" @click="change(item.soureid)">
					<router-link :to="{path:'jokeplay',query:{videourl:item.video,title:item.text,autimg:item.top_commentsHeader}}">
					<div class="flo"><img :src="item.thumbnail"></div>
					<div class="flo" style="margin-left: 10px;">
			  		 <div style="width:380px;font-size: 15px;line-height: 22px;margin:8px 0;color: black;">{{item.text}}</div>
						<p>{{item.username}} </p>
						<div class="comments">
							<span><i class="iconfont  icon-huo"></i>{{item.comment}}</span>
							<span>{{item.passtime}}</span>
						</div>
					</div>
					</router-link >
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				list: []
			};
		},
		created() {
			this.$http.get("https://www.apiopen.top/satinGodApi?type=1&page=1").then(response => {
				console.log(response.body.data)
				var a = []
				response.body.data.forEach(function(val, index, arr) {
					//console.log(val)
					if (val.video) {
						a.push(val)
					} else {

					}
					console.log(a)
				})

				// get body data

				this.list = a


			}, response => {
				// error callback
			});
		},
		methods:{
			change:function(id){
			this.$refs.ref.ajaxchange(id)
			}
		}
	}
</script>

<style scoped="">
	#joknav  li img {
		width: 210px;
		height: 118px;
	}

	#joknav li {
		overflow: hidden;
		padding: 0 0 15px 0;
		border: 1px solid #f8f8f8;
		/* background: #fff; */
	}

	.flo {
		float: left;
	}
	.flo p {font-size: 12px;
       color: #666;
	   margin-bottom: 20px;
	   margin-top: 18px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
	}
	.flo .comments{
		line-height: 16px;
    color:#666 ;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
	}
	.flo  div span {margin-right: 50px;}
</style>



