<template>
	<div id="fiction">
		<div class="panel panel-success">
			<div class="panel-heading" style="overflow: hidden;">
				<!-- <div class="panel-title"> -->
				<div style="float: left;font-size: 24px;"> 热门小说</div>
				<div style="float:right;"><input v-model="inpubox" type="text" style="height:35px;width: 300px;"><button 
					 style="height: 35px;border: none;width: 70px;"  v-on:click="searchbox">搜索</button></div>
				<!-- </div> -->

			</div>
			<div class="panel-body">
				<ul id="xiang">
					<li v-for="(item,index) in lists">
						<div class="b1"><img :src="item.cover"></div>
						<div class="b1 b2">
							<h4>{{item.title}}</h4>
							<div id="f1">
								<span>作者:{{item.author}}</span>
								<span>类型:{{item.category}}</span>
							</div>
							<div class="desc">{{item.desc}}</div>
							<div class="bth">
								<a class="shujai" v-on:click="locationbox(item.bid,item.chapterid_first)">开始阅读</a>
							</div>
						</div>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	import bus from "@/components/Bus.js"
	export default {
		data() {
			return {
				lists: [],
				inpubox: "",
				isshow:true
			};
		},
		methods:{
			searchbox:function(){
				 var vulebox=this.inpubox
				  var    url='https://www.apiopen.top/novelInfoApi?name='
				this.$http.get(url+vulebox).then(response => {
				console.log(response.body)
					this.lists=response.body.data.data
				
				}, response => {
					// error callback
				});
			},
			locationbox:function(id,cid){
				location.href="http://t.shuqi.com/route.php?pagename=bid=3290124#!/bid/"+id+"/cid/"+cid+"/ct/read"
			}
			
		},
		created() {
			let vulesbox = this.$route.query.book
			//console.log(vulesbox)
			let urls = 'https://www.apiopen.top/novelInfoApi?name='
			this.$http.get(urls + vulesbox).then(response => {
				console.log(response.body.data.data)
				// get body data
				this.lists = response.body.data.data;

			}, response => {
				// error callback
			});
		}
	}
</script>

<style>
	#xiang {
		overflow: hidden;
	}

	#xiang li {
		float: left;
		overflow: hidden;
		padding: 15px 15px;
		margin: 1px;
		text-align: center;
		
	}
	#xiang li .b1 img{
		width: 180px;
		height: 200px;
	}

	.b1 {
		
	}
	#f1{
		width: 207px;
		overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap
	}
	#f1 span {
		width: 200px;
		font-size: 12px;
		color: #888;
		line-height: 20px;
		margin-right:12px;
	}
	.desc{margin: 16px 0 0;
	font-size: 14px;
	line-height: 25px;
	color: #4c4c4c;
	width: 100%;}

	.b2 .desc {
		width: 200px;
		height: 70px;
	 width:200px;
     text-overflow:ellipsis;
     overflow:hidden;
     display:-webkit-box;
     -webkit-box-orient:vertical;
     -webkit-line-clamp:3;
	 margin-bottom:15px;
	}
	.bth .shujai{
		  margin-right: 10px;
		 background: #F60;
		 border: 1px solid #F60;
		 color: #4C4C4C;
		 width: 121px;
		 height: 28px;
		 line-height: 28px;
		 text-align: center;
		 font-size: 14px;
		 border-radius: 2px;
		 vertical-align: middle;
		 padding: 10px 20px;
		 text-decoration: none;
		     cursor: pointer;
	}
</style>



