<template>
	<div>
		<ul id="navs">
			<li v-for="item in list.slice((currentpage-1)*pagesize,pagesize*currentpage)">
				<router-link :to="{path:'/mvplayingbox',query:{playurl:item.url}}">
				<div class="imgbox"><img  class="imgboxs" :src="item.pic"><i class="iconfont  icon-yinpinbofanganniu1"></i></div>
				<div class="title">{{item.name}}</div>
				<div class="autor">{{item.singer}}</div>
				<div style="color:#999;">			
					{{item.publictime}}
				</div>
				</router-link>
			</li>
		</ul>
		<div id="pagebox">
						<el-pagination background layout="prev, pager, next" :total="50"  @size-change="handleSizeChange"
		@current-change="handleCurrentChange" >
						</el-pagination>
					</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				list:[],
				id:0,
				currentpage:1,
				pagesize:20
			};
		},
		created() {
		       
		
			this.$http.get('https://api.bzqll.com/music/tencent/hotMvList?key=579621905&year=0&tag='+this.id+'&area=0&limit=100&offset=0').then(
				response => {
		//console.log(response.body.data)
					this.list=response.body.data
					
				}, response => {
					// error callback
				});
		
		
		},
		methods:{
			changeajax(id){
				this.$http.get('https://api.bzqll.com/music/tencent/hotMvList?key=579621905&year=0&tag='+id+'&area=0&limit=100&offset=0').then(
						response => {
			//	console.log(response.body.data)
							this.list= response.body.data
							
						}, response => {
							// error callback
						});
				
			},
			handleSizeChange(val) {
			  //console.log(`每页  条`);
					this.pagesize=`${val}`
			},
			handleCurrentChange(val) {
			  console.log(`当前页: ${val}`);
					
					this.currentpage=`${val}`
					console.log(this.currentpage);
			}
		},
		computed:{
			
			
		
					
			
			
		}
	}
</script>

<style scoped="scoped">
	 #navs{
		 overflow: hidden;
	 }
	 #navs li{
		 float: left; 
		 width: 20%;
		 margin: 0  27px;
		 
	 }
#navs li .imgbox{
	  width: 225px;
	  height: 160px;
	  margin-bottom: 8px;
	  position: relative;
 }
 #navs li .imgboxs{
	  width: 225px;
	 height: 160px;
	 
 }
 
  #navs li  .title{
	  width: 225px;
	  overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
	font-weight: 400;
    zoom: 1;
	font-size: 16px;
	margin-bottom: 5px;
	color: #000;
  }
  #pagebox{
  	text-align: center;
  }
  #navs li   .autor{
	  width: 225px;
	  overflow: hidden;
	  text-overflow: ellipsis;
	  white-space: nowrap;
	   color: #999;
  }
  
#navs li .imgbox i{
	display: none;
	position: absolute;
    left: 50%;
    top: 50%;
  color: orange;
	font-size: 48px;
	margin-left: -24px;
	margin-top: -24px;
}
#navs li .imgbox:hover  i{
	display: block;
} 
#navs li .imgbox:hover .imgbox{
	box-shadow: 10px 10px 5px #888888;
	transform: scale(1) translateZ(0);
    transition-property: opacity,transform;
    transition-duration: .5s;
	
}
#navs li  a{
	text-decoration: none;
}
.router-link-active {
    text-decoration: none;
	color:greenyellow ;
}
</style>



