<template>
	<div id="fiction">
		<div class="panel panel-success">
			<div class="panel-heading" style="overflow: hidden;">
				<!-- <div class="panel-title"> -->
				<div style="float: left;font-size: 24px;"> 热门小说</div>
				<div style="float:right;"><input  v-model="inpubox"  type="text" style="height:35px;width: 300px;">
				<router-link :to="{path:'/Details',query:{book:inpubox}}" class="shidu" >
					<button  style="height: 35px;border: none;width: 70px;">搜索</button>
				</router-link>
				</div>
				<!-- </div> -->

			</div>
			<div class="panel-body">
				<ul id="nabox" v-show="isshow">
					<li v-for="(item,index) in list" class="row">
						<div class="col-md-3 imgbox"><img :src="item.book_cover"></div>
						<div class="col-md-9">
							<h4>{{item.bookname}}</h4>
							<div id="f1">
								<span>作者:{{item.author_name}}</span>
								<span>类型:{{item.class_name}}</span>
								<span>状态:{{item.stat_name}}</span>
								<span>字数:{{item.size}}字</span>
								<span>价格:{{item.price}}阅豆/千字</span>
							</div>
							<div class="desc">{{item.introduction}}</div>
							<div class="bth">
								<a class="shidu"  v-on:click="locationbox(item.bid,item.chapterid_first)">开始阅读</a>
								<a class="shujai" >加入书架</a>
							</div>
						</div>
					</li>
				</ul>
		
			</div>
		</div>
	</div>
</template>

<script>
	import bus from "@/components/Bus.js"
	export default {
		data() {
			
			return {
				list: [],
				inpubox:"",
				isshow:true
			};
		},
		created() {
			this.$http.get("https://www.apiopen.top/novelApi").then(response => {
				console.log(response.body.data)
				// get body data
				this.list = response.body.data;

			}, response => {
				// error callback
			});
		},
		methods:{
			/* searchbox:function(){
				 var vulebox=this.inpubox
				  var    url='https://www.apiopen.top/novelSearchApi?name='
				this.$http.get(url+vulebox).then(response => {
				//	console.log(response.body)
					
				
				}, response => {
					// error callback
				});
			} */
			locationbox:function(id,cid){
				location.href="http://t.shuqi.com/route.php?pagename=bid=3290124#!/bid/"+id+"/cid/"+cid+"/ct/read"
			}
		}
	}
</script>

<style scoped="">
	#nabox {
		overflow: hidden;
	}
    #nabox li  .imgbox{text-align: center;}
	#nabox li  .imgbox img{height: 210px; width: 180px;}

	#nabox li {margin: 30px 0;/* border: 1px solid #D1CDCD; */}

	#f1 span {
		width: 200px;
		font-size: 12px;
		color: #888;
		line-height: 20px;
		margin-right:20px;
	}
	.desc{margin: 16px 0 0;
    font-size: 14px;
    line-height: 25px;
    color: #4c4c4c;}
 .bth{margin-top: 20px;}
  .bth  a{text-decoration: none; color: #4C4C4C;}
 .bth .shidu{
	 margin-right: 10px;
	background: #F60;
    border: 1px solid #F60;
    color: #4C4C4C;
	width: 121px;
    height: 28px;
    line-height: 28px;
    text-align: center;
    font-size: 14px;
    border-radius: 2px;
	    cursor: pointer;
    vertical-align: middle;
	padding: 10px 20px;
	text-decoration: none;
 }
 .bth .shujai{
	  margin-right: 10px;
	 background: #F60;
	 border: 1px solid #F60;
	 color: #4C4C4C;
	 width: 121px;
	 height: 28px;
	 line-height: 28px;
	 text-align: center;
	 font-size: 14px;
	 border-radius: 2px;
	 vertical-align: middle;
	 padding: 10px 20px;
	 text-decoration: none;
 }
</style>



