<template>
	<div >
		<div class="" style="background:#3333;z-index:5; overflow: hidden;">


			<div class="floa1">

				个人主页
			</div>
			<div class="floa2">
				<ul id="nav">
					<li>
						<!-- <el-button type="text" @click="dialogTableVisible = true">登录</el-button> -->
					<el-button  style="color: #fff;font-size: 20px;" type="text" @click="dialogFormVisible = true">登录</el-button>


					</li>
					<li>注册</li>

				</ul>

			</div>

		<el-dialog title="" :visible.sync="dialogFormVisible" width=30%>
   <el-form ref="loginForm" :model="user" :rules="rules" status-icon label-width="80px">
            <el-form-item label="用户名" prop="name">
                <el-input v-model="user.name"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="pass">
                <el-input v-model="user.pass" type="password"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" icon="el-icon-upload" @click="login">登录</el-button>
            </el-form-item>
        </el-form>
</el-dialog>
		</div>
    

	
	</div>





</template>

<script>
	export default {
		data() {
			return {
				dialogTableVisible: false,
				dialogFormVisible: false,
				form: {
					name: '',
					region: '',
					date1: '',
					date2: '',
					delivery: false,
					type: [],
					resource: '',
					desc: ''
				},
				formLabelWidth: '50px',
				 user: {},
                rules: {
                    name: [
                        {required: true, message: '用户名不能为空', trigger: 'blur'}
                    ],
                    pass: [
                        {required: true, message: '密码不能为空', trigger: 'blur'}
                    ]
                }

			};
		},
		methods : {
			 login () {
                this.$refs.loginForm.validate((valid) => {
                    if (valid) {
                        if (this.user.name === 'admin' && this.user.pass === '123') {
                            this.$notify({
                                type: 'success',
                                message: '欢迎你,' + this.user.name + '!',
                                duration: 3000 
                            })
                            this.$router.replace('/')
                        } else {
                            this.$message({
                                type: 'error',
                                message: '用户名或密码错误',
                                showClose: true
                            })
                        }
                    }
                    else {
                        return false
                    }
                })
            }

		}


	};
</script>

<style >
	.floa1 {
		float: left;
		padding-left: 400px;
		color: #fff;
	}

	.floa2 {
		float: right;
	}

	#nav {}

	#nav li {
		float: left;
		padding: 0 20px;
		font-size: 20px;
		color: #fff;
	}
.el-dialog{
	z-index: 11;
}
	
#bgbox{
	  background: url("../assets/images/bg.jpg") no-repeat center fixed;
	 background-size: cover;
}

</style>



