<template>
	<div id="songlist">
				<ul id="navsong">
					<li v-for="item in list.slice((currentpage-1)*pagesize,pagesize*currentpage)">
						<div class="listbox">
					<router-link :to="{path:'/Songplay',query:{id:item.id}}" >	<div class="imgbox"><img :src="item.pic"></div></router-link >
						<div class="titlebox"    style="">{{item.name}}</div>
						<div class="autorbox" style="">{{item.singer}}</div>
						</div>
					</li>
				</ul> 
				<div id="pagebox">
					<el-pagination background layout="prev, pager, next" :total="100"  @size-change="handleSizeChange"
	@current-change="handleCurrentChange" >
					</el-pagination>
				</div>
			</div>
</template>

<script>
	export default {
		data() {
			return {
				list: [],
				currentpage:1,
				pagesize:10
			};
		},
		updated() {
			this.$http.get('https://api.bzqll.com/music/netease/search?key=579621905&s='+this.$route.query.value+'&type=song&limit=100&offset=0').then(response => {
			
	              this.list=response.body.data
		
			}, response => {
				// error callback
			});
		},
		methods:{
			 handleSizeChange(val) {
			  //console.log(`每页  条`);
					this.pagesize=`${val}`
			},
			handleCurrentChange(val) {
			  console.log(`当前页: ${val}`);
					
					this.currentpage=`${val}`
					console.log(this.currentpage);
			}
		}
	}
</script>

<style scoped="">
#songlist{
		padding: 0  10px;
	}
	#songlist #navsong  {
		overflow: hidden;
	}
	#songlist #navsong li {
		float: left;
		
	}
     #songlist #navsong li .listbox{
		 margin-left: 20px;
		 width: 224px;
	 }
	
	#songlist #navsong li img {
		width: 224px;
		height: 224px;
	}
	#songlist #navsong li .imgbox{
		margin-bottom: 15px;
	}
	
	#songlist #navsong li .titlebox{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 22px;
	font-size: 18px;
	margin-bottom: 10px;
	}
	#songlist #navsong li  .autorbox{
		width: 100%;
     color: #999;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 22px;
	}
	
	#pagebox{
		text-align: center;
	}
</style>



