<template>
	<div>
		<div id="songlist">
			<ul id="navsong">
				<li v-for="item in list.slice((currentpage-1)*pagesize,pagesize*currentpage)">
					<div class="listbox">
				<router-link :to="{path:'/Songplay',query:{id:item.id}}" >	<div class="imgbox"><i class="iconfont  icon-yinpinbofanganniu1"></i><img :src="item.pic"></div></router-link >
					<div class="titlebox"    style="">{{item.name}}</div>
					<div class="autorbox" style="">{{item.singer}}</div>
					</div>
				</li>
			</ul>
			<div id="pagebox">
				<el-pagination background layout="prev, pager, next" :total="100"  @size-change="handleSizeChange"
      @current-change="handleCurrentChange" >
				</el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				list: [],
				currentpage:1,
				pagesize:10
			};
		},
		created() {
			this.handleUserList()
			
			
		},
		methods: {
      handleSizeChange(val) {
        //console.log(`每页  条`);
		this.pagesize=`${val}`
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
		
		this.currentpage=`${val}`
		console.log(this.currentpage);
      },
	  handleUserList(){
		  this.$http.get("https://api.bzqll.com/music/netease/songList?key=579621905&id=3778678&limit=10&offset=0").then(
				response => {
					console.log(response.body.data.songs)
                    const  a=this.pagesize
					  const  b=this.currentpage
					  console.log(a)
					this.list =response.body.data.songs

				}, response => {
					// error callback
				});
	  }
    }
	}
</script>

<style scoped="">
	#songlist{
		padding: 0  5px;
		min-width: 1250px;
	}
	#songlist #navsong  {
		overflow: hidden;
	}
	#songlist #navsong li {
		float: left;
		
	}
     #songlist #navsong li .listbox{
		 margin-left: 15px;
		 width: 224px;
	 }
	
	#songlist #navsong li img {
		width: 224px;
		height: 224px;
	}
	#songlist #navsong li .imgbox{
		margin-bottom: 15px;
		position: relative;
	}
	
	#songlist #navsong li .titlebox{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 22px;
	font-size: 18px;
	margin-bottom: 10px;
	}
	#songlist #navsong li  .autorbox{
		width: 100%;
     color: #999;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 22px;
	}
	
	#pagebox{
		text-align: center;
	}
	.icon-yinpinbofanganniu1{
		display: none;
			position: absolute;
		  left: 50%;
		  top: 50%;
		color: orange;
			font-size: 48px;
			margin-left: -24px;
			margin-top: -24px;
	}
	.imgbox:hover .icon-yinpinbofanganniu1{
		display: block;
	}
</style>



