			   <div class="com-f1">
				   <div style="margin-bottom: 35px;">
					   <textarea v-model="content"></textarea>
				   </div>
				   <div>
					   <span style="margin-right: 20px;">
						   <i class="iconfont icon-biaoqing1"></i>
						   表情
