					   <span>
					   		 <i class="iconfont  icon-tupian"></i>
					   		 图片
					   </span>
					   <span style="float: right;">
						   <button v-on:click="submit">发布</button>
					   </span>
				   </div>
			   </div>
			   <div class="comment-f2">
