						      <span> {{item.ctime}}</span>
						   </div>
						   <div class="com-f1-2">{{item.content}}</div>
						   <div style="float: right; text-align: right;width: 550px;">
							   <span>
								   <i class="iconfont icon-pinglun"></i>评论
							   </span>
							   <span style="margin-left: 15px;">
							   	<i class="iconfont icon-heart-fill"></i>点赞
							   </span>
						   </div>
					   </div>
				   </div>
			   </div>
		   </div>
	</div>
</template>

