				});
			
		},
		methods:{
			submit:function(){
				
				     var now = new Date();
       
        var year = now.getFullYear();       //年
        var month = now.getMonth() + 1;     //月
        var day = now.getDate();            //日
        var hh = now.getHours();            //时
        var mm = now.getMinutes();
       var  ss =now.getSeconds();;          //分
        var clock = year + "-";
        if(month < 10){
            clock += "0" +month+"-" ; 
           
        }else {
        	 clock += month + "-";
        }
		
       if(day < 10){
        	 clock += "0"+day+" ";
          
        }else {
        	clock += day + " ";
        }

        if(hh < 10){

            clock += "0"+hh;
           
        }else {
        	 clock += hh + ":";
        }
           
        
        if (mm < 10){
        	clock += '0'+ mm +":"; 
        	
        }else {
        	clock += mm +":"; 
        } 

        if (ss < 10){
        	clock += '0'+ss; 
        	
        }else {
        	clock += mm; 
        }
				console.log(this.content)
				// var comment = { id:Date.now(), user:this.user,content:this.content};
				 var newcomment = { content:this.content,ctime:clock,user:{profile_image:"http://wimg.spriteapp.cn/p/jie.jpg",username:"ms汤"}};
				// var newprofile_image={profile_image:"http://wimg.spriteapp.cn/p/jie.jpg"}
				this.list.unshift(newcomment)
				this.content=""
			},
			
			
			ajaxchange:function(id){
				this.$http.get("https://www.apiopen.top/satinCommentApi?id="+id+"&page=1").then(response => {
					console.log(response.body.data.normal.list)
					
				        this.list=response.body.data.normal.list
						
						
				}, response => {
					// error callback
				});
			}
		}


