<style>
	.video-js .vjs-big-play-button {}
	.container {
    background-color: #efefef;
    min-height: 100%;
  }
  
  #commentpage h3{
	  font-size: 24px;
    color: #222;
  }
    #commentpage .com-f1{
		padding: 9px 14px 9px 15px;
    border: 1px solid #e1e3e6;
    border-radius: 5px;
    box-shadow: 0 0 4px 2px hsla(0,0%,92%,.35);
}

#commentpage .com-f1 textarea{
	width: 100%;
    height: 60px;
    border: 0;
    outline: 0;
    resize: none;
    color: #777;
    overflow: auto;
    line-height: 26px;
	 vertical-align: top;
	 font-family: inherit;
}
#commentpage .com-f1  div span i{
	    width: 50px;
    height: 50px;
	margin-right: 9px;
    vertical-align: top;
    cursor: pointer;
	
}
#commentpage .com-f1  div span button{
	width: 90px;
    height: 34px;
    line-height: 34px;
    font-size: 16px;
    text-align: center;
    color: #fff;
    background-color: #00be06;
    border-radius: 17px;
	border: none;
}
 .comment-f2 .comment-s{
	 overflow: hidden;
 }
#commentpage  .comment-f-f1{
	float: left;
}
#commentpage  .comment-f-f1 img{
	    border-radius: 50%;
		border: none;
    vertical-align: top;
	    width: 54px;
    height: 54px;
	margin: 5px 5px 5px 0;
}
#commentpage  .comment-f-f1  div{
	width: 100%;
}
	#commentpage  .comment-f-f1  .com-f1-1{
		height: 18px;
    line-height: 18px;
	font-size: 18px;
    color: #222;
	margin: 8px 0 11px;
}
	#commentpage  .comment-f-f1  .com-f1-2{
		font-size: 14px;
    line-height: 26px;
	 color: #222;
	}

	#commentpage  .comment-f-f1  .com-f1-1 span{
		margin-left: 60px;
		font-size: 12px;
		    color: #999;
	}
	.comment-s{
		padding-top: 18px;
    padding-bottom: 19px;
	border-bottom: 2px dashed green;
	}
</style>



